                plugin.isOpen = false;

                // check that the callback passed in is really a function & handle namespaced functions
                if (plugin.options.closeCallback && !_.isFunction(plugin.options.closeCallback)) {
                    plugin.options.closeCallback = helpers.getFuncFromString(plugin.options.closeCallback);
                }
                plugin.options.closeCallback();

