/*
* Code for handling WFM lead forms being pulled into a page via ajax/panelizr
*
* NOTE: Everything should now be self-contained to this code file and completely separate from panelizr code.
*       i.e. panelizr doesn't know or care if it is loading in a lead form, it just does ajax loading of some content
*       and we set it to call the leadFormInit callback for any lead forms. 
*       THIS CODE, however, will only work for WFM forms pulled in via panelizr and expects certain parent elements, etc.
*
* REQUIREJS NOTE: Setting a global var like this is pretty crappy, we shouldn't do this much.
*/
define('core/wfm/leadforms',["jquery", "core/global/compensatrs", "core/ef_plugins/spinnerhelper", "libs/jquery.cookie"], function ($, compensatr) {
    var EFTOURS = window.EFTOURS || {};

    return EFTOURS.leadforms = (function () {
        // helper to get the form item name for analytics
        function getFormNameForAnalytics($form) {
            return $form.find('#leadFormName').val();
        }

        function scrollBackToPanel($panelTop) {
            if ($panelTop.length > 0) {
                window.scrollTo(0, $panelTop.offset().top + -20);
            }
        }
        return {
            leadFormSpinner: null,
            isCta: false,

            // Callback for initial loading of lead form into panelizr *******************
            leadFormInit: function ($panel, response, status, xhr, cta) {
                isCta = cta;
                var $form = $panel.find('form'),
                    formName = getFormNameForAnalytics($form),
                    updateTarget;

                // set form's update target id
                if (isCta) {
                    updateTarget = '#' + $panel.attr('id') + ' .cta-body';
                } else {
                    updateTarget = '#' + $panel.attr('id') + ' .panel-body';
                }
                $form.attr('data-ajax-update', updateTarget);
                compensatr.handleTelLinks();
            },

            // Callbacks for lead form unobtrusive ajax submission *******************
            // OnBegin 
            leadFormBegin: function (xhr) {
                //    console.log(xhr);
                //    console.log(this);
                //    console.log($(this));
                var $form = $(this),
                    self = this;

                // scroll to the top of the panel
                if (typeof (isCta) != "undefined") {
                    if (!isCta) {
                        $panelTop = $('#' + $form.closest('.panel').attr('id') + ' .panel-header');
                        scrollBackToPanel($panelTop);
                    }
                }

                // disable re-submission of form
                $form.find(":submit").attr("disabled", "disabled");

                if (this.leadFormSpinner) {
                    this.leadFormSpinner = this.leadFormSpinner.destroy();
                }
                // start the spinner
                this.leadFormSpinner = $form.closest('.panel').spinnerhelper({ 'type': 'overlay', 'delay': 100 }).data('plugin_spinnerhelper');

            },

            // OnComplete (always fires whether there was an error or not)
            leadFormComplete: function (xhr, status) {
                var $form = $('#' + this.getAttribute('id')), // reselect form in case the initial calling element is no longer in the DOM
                    updateTarget;
                // stop the spinner
                if (this.leadFormSpinner) {
                    this.leadFormSpinner = this.leadFormSpinner.destroy();
                }

                // set form's update target id
                if (isCta) {
                    updateTarget = '#' + $form.closest('.panel').attr('id') + ' .cta-body';
                } else {
                    updateTarget = '#' + $form.closest('.panel').attr('id') + ' .panel-body';
                }
                $form.attr('data-ajax-update', updateTarget);
                
            },

            // OnSuccess (fires only if load was successful)
            leadFormSuccess: function (data, status, xhr) {
                var $form = $(this),
                    formName = getFormNameForAnalytics($form),
                    // Get the root domain, tge part of the domain name before second-to-last dot. Typically ".eftours.com." That way, the cookie works across subdomains.
                    // Given www.eftours.com, eftours.com, landing.eftours.com, or blah.foo.bar.eftours.com, the root domain should be .eftours.com.
                    domain = document.domain.toString(),
                    domainPieces = domain.split('.'),
                    rootDomain = "." + domainPieces[domainPieces.length - 2] + "." + domainPieces[domainPieces.length - 1];
                if ($('.form-success').length > 0) {
                    dataLayer.push({ 'event': 'load', 'context': formName + ' Confirmation', 'pageType': 'virtual' });
                    // Sets cookie to prevent disruptr -- this cookie and logic is shared with scripts in liveball
                    // For compatibility with Liveball, we'll temporarily set raw = true.
                    // This prevents jQuery cookie from URI encoding the cookie value.
                    $.cookie.raw = true;
                    var cookieString = "Count=3&Disable=true";
                    $.cookie('EFFEDCK', cookieString, {
                        expires: 180,
                        path: '/',
                        domain: rootDomain
                    });
                    // Return jQuery cookie to raw = false, in case other scripts use it.
                    $.cookie.raw = false;
                }
            },

            // OnFailure (fires if load failed)
            leadFormFailure: function (xhr, status, error) {
                var $form = $(this),
                    formName = getFormNameForAnalytics($form);

                dataLayer.push({ 'event': 'load', 'context': formName + ' Error', 'pageType': 'virtual' });
            }
        };
    }());
});
